.section {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	width: 100%;

	background: linear-gradient(180deg,
			rgba(196, 196, 196, 0) 25%,
			#bedbb0 92.19%);
}

.wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 20px;
	width: 310px;
}

@media (min-width: 375px) {
	.wrapper {
		width: 335px;
	}

}

@media (min-width: 768px) {
	.wrapper {
		width: 473px;
	}
}

.imgUser {
	width: 124px;
	height: 124px;
}

@media (min-width: 768px) {
	.imgUser {
		width: 162px;
		height: 162px;
	}

}

.login {
	text-decoration: none;
	color: #161616;
	font-size: 14px;
	font-weight: 500;
	letter-spacing: -0.28px;
	width: 344px;
	height: 41px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 8px;
	transition: all 0.3s ease-in-out;

}

.login:hover,
.login:focus {
	opacity: 0.8;
	background: #161616;
	color: #ffffff;
}

.register {
	width: calc(100% - 40px);
	padding: 14px;
	margin-bottom: 14px;
	text-decoration: none;
	color: #ffffff;
	text-align: center;
	font-size: 14px;
	font-weight: 500;
	letter-spacing: -0.28px;

	border-radius: 8px;
	background: #161616;

	transition: all 0.3s ease-in-out;
}

.register:hover {
	opacity: 0.8;
}

@media (min-width: 768px) {
	.register {
		width: 344px;
	}
}

.wrapLogo {
	display: flex;
	align-items: center;
	gap: 14px;
	height: 42px;
	margin-top: 14px;


}

@media (min-width: 768px) {
	.wrapLogo {
		margin-top: 24px;
		height: 60px;
	}
}

.logoIcon {
	width: 40px;
	height: 40px;
}

@media (min-width: 768px) {
	.logoIcon {
		width: 48px;
		height: 48px;
	}
}

.text {
	margin-top: 24px;
	margin-bottom: 48px;
	text-align: center;
	color: #161616;
	font-size: 14px;
	line-height: 1.28;
	letter-spacing: -0.28px;
}